import {URL} from 'url';
import {library} from '@fortawesome/fontawesome-svg-core'
import {faGoogle} from '@fortawesome/free-brands-svg-icons'
import {faFacebookF} from '@fortawesome/free-brands-svg-icons'
import {faQuestionCircle} from "@fortawesome/free-regular-svg-icons";

library.add(faQuestionCircle)
library.add(faGoogle)
export default {
  name: 'Index',
  data() {
    return {
      value: 'Some value',
      busy: false,
      timeout: null,
      userId: 0,
      msgTextBox: "",
      currentChatRoom: null,
      showChatMessagesOverlay: false,
      messagesHtml: [],
      visibleMain: true,
      visibleProgress: false,
      intervalX: null,
      filter: null,
      filterOn: [],
      fields: [
        {
          key: 'viewName',
          label: 'Name',
          sortable: true,
          thStyle: {width: '25%'},
        },
        {
          key: 'text',
          label: 'Message',
          sortable: true,
          thStyle: {width: '50%'},
        },
        {
          key: 'updatedAt',
          label: 'Date',
          sortable: true,
          thStyle: {width: '25%'},
        },
      ],
      request_rows: []
    }
  },
  mounted() {
    this.showRequests()
    this.intervalX = setInterval(this.refreshTimer, 20 * 1000);
    this.userId = this.$auth.user.id
  },
  methods: {

    rowClass(item, type) {
      if (!item || type !== 'row') return
      let font = ""
      if (item.unread > 0)
        font = 'font-weight-bold font-italic w-100'
      return 'mt-7 ' + font
    },

    showRequests() {
      try {
        if (!this.visibleMain)
          return
        this.visibleProgress = true
        this.$axios.post(`request/getRequests`, null).then((res) => {
          this.visibleProgress = false
          this.request_rows = res.data.data

        }).catch((err) => {
          this.visibleProgress = false
        });

      } catch (e) {
        console.log(e);
        this.visibleProgress = false
      }
    }
    ,
    onclick(row, index) {
      try {
        let sUid = this.$auth.user.id
        if (sUid === row.receiverId)
          sUid = row.senderId
        else
          sUid = row.receiverId

        this.visibleProgress = false
        this.showChatMessagesOverlay = true
        let reqData = {sUid: sUid}
        this.currentChatRoom = row

        this.$axios.post(`request/getUserRequests`, reqData).then((res) => {
          if (res.data.data.length > 0) {
            this.messagesHtml = res.data.data
          }
          this.visibleMain = false;
          this.showChatMessagesOverlay = false

          this.$axios.post(`/request/setview`, reqData).then((res) => {
            row.viewed = 1

          }).catch(error => {

          })


        }).catch((err) => {
          this.showChatMessagesOverlay = false
          this.visibleMain = true;
        });

      } catch (e) {
        this.showChatMessagesOverlay = false
      }
    }
    ,
    sendMessage() {
      try {
        if (this.msgTextBox.length === 0)
          return

        let reqData = {
          receiverId: this.currentChatRoom.receiverId === this.userId ? this.currentChatRoom.senderId : this.currentChatRoom.receiverId,
          text: this.msgTextBox,
          type: 0, // simple message
          entityId: 0
        }
        var options = {
          container: '#chatbox',
          easing: 'ease-in',
          lazy: false,
          offset: -60,
          force: true,
          cancelable: true,
          onStart: function (element) {
            // scrolling started
          },
          onDone: function (element) {
            // scrolling is done
          },
          onCancel: function () {
            // scrolling has been interrupted
          },
          x: false,
          y: true
        }

        this.$axios.post(`/request/create`, reqData).then((res) => {
          if (res.data.ok === true) {
            this.msgTextBox = ""
            this.messagesHtml.push(res.data.data)
            let element = '#msg_' + (res.data.data.id - 1)
            var VueScrollTo = require('vue-scrollto');
            VueScrollTo.scrollTo(element, 100, options)


          }

        }).catch((err) => {
          console.log(err)
        });
      } catch (e) {

      }
    }
    ,
    chatMessages() {

    },
    cDate(date) {
      return date.toLocaleString()
    }
    ,
    back() {
      this.visibleMain = true
      this.visibleProgress = true

    },
    refreshTimer() {
      console.log('----hi------')
      if (this.visibleMain) {
        this.showRequests();
      }
    }

  },
  beforeDestroy() {
    console.log('----bye------')
    clearInterval(this.intervalX)
  },
}
